@import 'styles/media.scss';
.root {
  margin-right: 30px;
}
.radio {
  position: absolute;
  z-index: -1;
  opacity: 0;
  & + label {
    display: inline-flex;
    align-items: center;
    user-select: none;
    &:before {
      content: '';
      display: inline-block;
      width: 24px;
      height: 24px;
      flex-shrink: 0;
      flex-grow: 0;
      border: 2px solid #1b1f2b;
      border-radius: 50%;
      margin-right: 0.5em;
      background-repeat: no-repeat;
      background-position: center center;
      background-size: 50%;
      @include media(sm) {
        width: 16px;
        height: 16px;
      }
    }
  }
  &:checked + label::before {
    border-color: #1b1f2b;
    background-image: url(../../../../public/img/svg/radio.svg);
  }
  &:disabled + label::before {
    border-color: #1b1f2b;
  }
}
